import Topbar from '../../components/topBar/topbarx.vue'
export default {
	data() {
		return {
			title: '新增常用旅客',
			name: '蒋治坤',
			phone: '17683059017',
			id_card: '513922199702111997',
			card_end_time: '2020/04/19'
		}
	},
	onLoad() {

	},
	onUnload(){
		uni.hideLoading()
	},
	methods: {
		// 提交
		subClick(){
			let _this = this
			let userinfo = this.$store.state.userInfo
			if(this.name && this.phone && this.id_card && this.card_end_time){
				// 满足条件
				uni.request({
					url: this.$http + '/api/user/addPassenger',
					method: 'POST',
					data: {
						token: userinfo.token,
						name: _this.name,
						phone: _this.phone,
						id_card: _this.id_card,
						card_end_time: _this.card_end_time,
						// user_id: userinfo.user_id
					},
					success(res) {
						console.log('添加常用旅客返回数据', res)
						if(res.data.code == 1){
							uni.showModal({
								title: '提示',
								content: '添加成功'
							})
							uni.navigateBack({
								
							})
						}else{
							uni.showModal({
								title: '提示',
								content: res.data.msg
							})
						}
					}
				})
			}else{
				uni.showModal({
					title: '提示',
					content: '信息未填全'
				})
			}
		}
	},
	components: {
		Topbar,
	}
}
